/**
 * @name isSortableActive
 * @type {function}
 *
 * @description
 * Helper to check if header sortable column is active or not
 *
 * @param {object} sortable Sortable object
 * @param {object} $ctrl Controller object
 *
 * @returns {boolean} True if sortable column is active
 */
export const isSortableActive = (sortable, $ctrl) =>
  sortable && (sortable.key === $ctrl.state.directDebits.orderBy);

const statuses = {
  large: {
    ANNOUNCED: {
      translationKey: 'announced',
      class: 'unauthorized',
    },
    PROCESSED: {
      translationKey: 'processed',
      class: 'authorized',
    },
    REJECTED: {
      translationKey: 'rejected',
      class: 'rejected',
    },
    REFUNDED: {
      translationKey: 'refunded',
      class: 'authorized',
    },
    REFUSED: {
      translationKey: 'refused',
      class: 'rejected',
    },
    REFUND_REQUESTED: {
      translationKey: 'refundRequested',
      class: 'pending',
    },
    REFUSAL_REQUESTED: {
      translationKey: 'refusalRequested',
      class: 'pending',
    },
  },
  small: {
    ANNOUNCED: {
      translationKey: 'A',
    },
    PROCESSED: {
      translationKey: 'P',
    },
    REJECTED: {
      translationKey: 'R',
    },
    REFUNDED: {
      translationKey: 'RF',
    },
    REFUSED: {
      translationKey: 'RB',
    },
    REFUND_REQUESTED: {
      translationKey: 'RFR',
    },
    REFUSAL_REQUESTED: {
      translationKey: 'RR',
    },
  },
};

const actionKeys = {
  ANNOUNCED: 'refuse',
  PROCESSED: 'refund',
};

const actionIcons = {
  ANNOUNCED: 'fa-ban',
  PROCESSED: 'fa-undo',
};

/**
 * @name isStatusDisplayed
 * @type {function}
 *
 * @description
 * Helper to check if status displayed or not
 *
 * @param {string} status Direct debit status
 *
 * @returns {boolean} True if status must be displayed
 */
export const isStatusDisplayed = (status) => !!statuses.large[status];

/**
 * @name getStatusLabel
 * @type {function}
 *
 * @description
 * Helper to get status label
 *
 * @param {string} status Direct debit status
 * @param {string} type of status label (small, large)
 *
 * @returns {string} Translation key for status label
 */
export const getStatusLabel = (status, type = 'large') =>
  `directDebit.status.${statuses[type][status].translationKey}`;


/**
 * @name getStatusClass
 * @type {function}
 *
 * @description
 * Helper to get status class
 *
 * @param {string} status Direct debit status
 *
 * @returns {string} Class for status
 */
export const getStatusClass = status => statuses.large[status].class;

/**
 * @name hasAction
 * @type {function}
 *
 * @description
 * Helper to check if direct debit has action
 *
 * @param {string} status Direct debit status
 *
 * @returns {boolean} True if direct debit has action
 */
export const hasAction = status => !!actionKeys[status];

/**
 * @name getActionLabel
 * @type {function}
 *
 * @description
 * Helper to get action label according status
 *
 * @param {string} status Direct debit status
 *
 * @returns {string} Translation key for action label
 */
export const getActionLabel = status => `directDebit.action.${actionKeys[status]}`;

/**
 * @name getActionIcon
 * @type {function}
 *
 * @description
 * Helper to get action icon according to status
 *
 * @param {string} status Direct debit status
 *
 * @returns {string} Translation key for action icon
 */
export const getActionIcon = status => actionIcons[status];

/**
 * @name getConfirmTitle
 * @type {function}
 *
 * @description
 * Helper to get confirm title text according status
 *
 * @param {string} status Direct debit status
 *
 * @returns {string} Translation key for confirm title text
 */
export const getConfirmTitle = status => `directDebit.dialog.${actionKeys[status]}.title`;

/**
 * @name getConfirmBody
 * @type {function}
 *
 * @description
 * Helper to get confirm body text according status
 *
 * @param {string} status Direct debit status
 *
 * @returns {string} Translation key for confirm body text
 */
export const getConfirmBody = status => `directDebit.dialog.${actionKeys[status]}.body`;

/**
 * @name getConfirmSubmit
 * @type {function}
 *
 * @description
 * Helper to get submit confirm label according status
 *
 * @param {string} status Direct debit status
 *
 * @returns {string} Translation key for submit confirm label
 */
export const getConfirmSubmit = status => `directDebit.dialog.${actionKeys[status]}.yes`;

const frequencyKeys = {
  ONCE: 'once',
  DAILY: 'daily',
  WEEKLY: 'weekly',
  MONTHLY: 'monthly',
  YEARLY: 'yearly',
};

/**
 * @name getFrequencyLabel
 * @type {function}
 *
 * @description
 * Helper to get direct debit frequency
 *
 * @param {string} frequency Direct debit frequency
 *
 * @returns {string} Translation key for frequency label
 */
export const getFrequencyLabel = (mode, frequency) =>
  `directDebit.frequency.${mode === 'SINGLE' ? frequencyKeys.ONCE : frequencyKeys[frequency]}`;

/**
 * @name addDayNumberToDate
 * @inner
 * @type {function}
 *
 * @description
 * Adds days count to date
 *
 * @param {string} date Date string
 * @param {number} dayNumber Days count
 *
 * @returns {number} New timestamp with added days
 */
const addDayNumberToDate = (date, dayNumber) => {
  const parsedDate = Date.parse(date);
  const parsedDayNumber = dayNumber * 1000 * 60 * 60 * 24;

  return parsedDate + parsedDayNumber;
};

/**
 * @name getRefundDate
 * @type {function}
 *
 * @description
 * Helper to get refund deadline if available
 *
 * @param {object} item Direct debit object
 *
 * @returns {null|number} New timestamp with added days or null if is not applicable
 */
export const getRefundDate = (item) =>
  (item.status === 'PROCESSED' ? addDayNumberToDate(item.date, item.refundDayCount) : null);

/**
 * @name toggled
 * @inner
 * @type {null|string}
 *
 * @description
 * Stores id of toggled direct debit
 */
let toggled = null;

/**
 * @name canBeToggled
 * @inner
 * @type {function}
 *
 * @description
 * Check if current click event allows toggle direct debit
 *
 * @param {object} event click object
 *
 * @returns {boolean} True if event allows toggle direct debit
 */
const canBeToggled = (event) => {
  const target = event.target.nodeName.toUpperCase();
  const parent = event.target.parentNode.nodeName.toUpperCase();
  const unavailableNodes = ['INPUT', 'BUTTON', 'A'];

  return unavailableNodes.indexOf(target) === -1
    && unavailableNodes.indexOf(parent) === -1
    && !event.view.getSelection().toString();
};

/**
 * @name toggleDetails
 * @type {function}
 *
 * @description
 * Show/hide (toggle) direct debit details row
 *
 * @param {string} id row identification
 * @param {object} event click object
 */
export const toggleDetails = (id, event) => {
  if (canBeToggled(event)) {
    toggled = (id === toggled) ? null : id;
  }
};

/**
 * @name isToggled
 * @type {function}
 *
 * @description
 * Helper to check if direct debit is toggled or not
 *
 * @param {string} id row identification
 *
 * @returns {boolean} True if direct debit is toggled
 */
export const isToggled = (id) => id === toggled;

/**
 * @name removeNotification
 *
 * @description
 * Helper to remove notification from the list
 *
 * @param {object} notification
 * @param {Array} notificationsList
 * @type {function}
 * @returns {null}
 */
export const removeNotification = (notification, notificationsList) =>
  notificationsList.filter((item) => item !== notification);

const notificationClasses = {
  SUCCESS: 'bg-success',
  ALERT: 'bg-warning',
};

/**
 * @name getNotificationClass
 *
 * @description
 * Helper to get notification class
 *
 * @param {string} level Notification level
 * @type {function}
 * @returns {string}
 */
export const getNotificationClass = (level) => notificationClasses[level];

/**
 * @description
 * Checkes if actual pagination type matches the one, defined in properties
 *
 * @public
 * @name isPaginationTypeMatch
 * @type {function}
 * @param {function} $ctrl      Current controller
 * @param {string} type         Description of pagination type (pagination, load-more)
 *
 * @returns {boolean}
 */
export const isPaginationTypeMatch = ($ctrl, type) =>
  $ctrl.state.pageParams.paginationType === type;



